import { Application } from 'pixi'

console.log("hello");
      const { innerWidth: width, innerHeight: height } = window;
      let keyDown;
      const cursorRadius = 50;
      const speed = 10;
      let direction = { x: 0, y: 0 };
      let position = { x: (width / 2) - (cursorRadius / 2), y: (height / 2) - (cursorRadius / 2) };
      document.addEventListener('keydown', ({ code }) => {
        switch(code) {
          case 'ArrowLeft':
          case 'KeyA':
            direction.x = -1 * speed;
            break;
          case 'ArrowRight':
          case 'KeyD':
            direction.x = 1 * speed;
            break;
          case 'ArrowUp':
          case 'KeyW':
            direction.y = -1 * speed;
            break;
          case 'ArrowDown':
          case 'KeyS':
            direction.y = 1 * speed;
            break;
          default:
            keyDown = code;
            break;
        }
      });
      document.addEventListener('keyup', ({ code }) => {
        keyDown = undefined
        switch(code) {
          case 'ArrowLeft':
          case 'KeyA':
          case 'ArrowRight':
          case 'KeyD':
            direction.x = 0;
            break;
          case 'ArrowUp':
          case 'KeyW':
          case 'ArrowDown':
          case 'KeyS':
            direction.y = 0;
            break;
          default:
            keyDown = code;
            break;
        }
      });

      let app = new PIXI.Application({ width: width - 20, height: height - 20 });
      document.body.appendChild(app.view);

      const cursorGraphic = new PIXI.Graphics();
      // Draw cursor
      cursorGraphic.beginFill('0xff2222');
      cursorGraphic.drawCircle(position.x, position.y, cursorRadius);
      cursorGraphic.endFill();
      const renderer = PIXI.autoDetectRenderer();

      const texture = app.renderer.generateTexture(cursorGraphic);
      const cursor = new PIXI.Sprite(texture);
      app.stage.addChild(cursor);


      const basicStyle = new PIXI.TextStyle({
        fontSize: 18,
        fill: ['#ffffff'],
      });
      const basicText = new PIXI.Text(`KeyDown: ${keyDown}`, basicStyle);
      basicText.x = 20;
      basicText.y = 20;
      app.stage.addChild(basicText);

      let elapsed = 0.0;

      app.ticker.add((delta) => {
        elapsed += delta;

        position.x = position.x + direction.x;
        position.y = position.y + direction.y;

        console.log(position, direction)
        if (position.x < 0) {
          position.x = 0;
        }

        if (position.x > width) {
          position.x = width;
        }

        if (position.y < 0) {
          position.y = 0;
        }

        if (position.y > height) {
          position.y = height;
        }

        cursor.x = position.x;
        cursor.y = position.y;

        basicText.text = `KeyDown: ${keyDown ? keyDown : ''}`;
      });
